<template>
    <div class="withdraw-options">
        <template v-if="options.length">
            <div
                :class="[
                    'withdraw-options__option',
                    {guide: computeGuideClass(index)},
                    { active: computeActiveClass(option) },
                    { disabled: computeDisabledClass},
                ]"
                v-for="(option,index) in options"
                :key="`${option.type}-${option.cash}`"
                @click="clickHandler(option)">
                <div :class="`withdraw-options__type ${option.type}`">
                    {{ getType(option.type) }}
                </div>
                <div
                    v-if="option.bubbleMsg"
                    class="withdraw-options__bubble">{{ option.bubbleMsg }}</div>
                <div class="withdraw-options__info">
                    <div class="withdraw-options__cash">{{ option.cash | formatMoney }}元</div>
                    <div
                        v-if="option.msg"
                        class="withdraw-options__msg"
                        v-html="option.msg" />
                    <div
                        v-if="option.savingMsg"
                        class="withdraw-options__saving-msg">
                        {{ option.savingMsg }}
                    </div>
                </div>
            </div>
            <div
                v-if="!isnewuser"
                class="withdraw-options__option withdraw-coupon"
                @click="clickCoupon"
            >
                <i
                    v-if="!newImei"
                    class="withdraw-coupon__hint tada2-infinite" />
            </div>
        </template>
        <div
            v-else
            :class="{ 'withdraw__empty-newuser' :isnewuser}"
            class="withdraw__empty">
            <div class="withdraw__empty-icon" />
            <p class="withdraw__empty-text">
                {{ isnewuser ? '阅读领红包，账户暂无现金可提现快去阅读吧': '微信提现和话费充值正在升级中，请耐心等待~' }}
            </p>
        </div>
    </div>
</template>

<script>
import { openCommonWebViewByPath } from '@/utils/jsApiV2/client';

export default {
    name: 'WithdrawOptions',
    data() {
        return {
            typeMap: Object.freeze({
                wechat: '微信',
                phonenumber: '话费',
            }),
            isnewuser: this.$route.query.channel,
        };
    },
    props: {
        value: {
            type: Object,
            default: () => ({}),
        },
        options: {
            type: Array,
            default: () => ([]),
        },
        cash: {
            type: Number,
            default: 0,
        },
        newImei: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        computeDisabledClass() {
            return this.value && !this.value.canClick;
        },
    },
    filters: {
        formatMoney(value) {
            return value / 100;
        },
    },
    methods: {
        computeGuideClass(index) {
            return this.newImei && index === 0;
        },
        computeActiveClass(option) {
            return this.value && this.value.type === option.type && this.value.cash === option.cash;
        },
        clickHandler(cash) {
            if (cash.canClick) {
                this.$emit('input', cash);
            }
        },
        getType(type) {
            return this.typeMap[type];
        },
        clickCoupon() {
            this.$o2o({
                e_c: '提现兑换入口',
                e_a: '点击',
                e_n: '金币兑换现金券',
            });
            openCommonWebViewByPath('/mobile-v2/act/pddExchange');
        },
    },
    created() {
        // 新用户7天不曝光金币兑换现金券
        if (!this.isnewuser) {
            this.$o2o({
                e_c: '提现兑换入口',
                e_a: '曝光',
                e_n: '金币兑换现金券',
            });
        }
    },
};
</script>

